


























































































// 审核以及详情未完成

import { Component, Vue, Watch } from "vue-property-decorator";
import { RequestPaging } from "@/utils/util";
import { disType } from "@/utils/type";
import lsDialog from "@/components/ls-dialog.vue"
import lsPagination from "@/components/ls-pagination.vue"
import DatePicker from "@/components/date-picker.vue"
import ExportData from '@/components/export-data/index.vue'
import { apiDistributionApplyLists, apiDistributionApplyRefuse, apiDistributionApplyPass } from "@/api/distribution/distribution";

@Component({
    components: {
        lsDialog,
        lsPagination,
        DatePicker,
        ExportData
    },
})
export default class DistributionApply extends Vue {
    apiDistributionApplyLists = apiDistributionApplyLists

    tabs = [
        {
            label: "全部",
            name: disType[0],
        },
        {
            label: "待审核",
            name: disType[1],
        },
        {
            label: "审核通过",
            name: disType[2],
        },
        {
            label: "审核拒绝",
            name: disType[3],
        },
    ];

    queryObj = {
        user_info: "",
        end_time: "",
        start_time: "",
    };



    examine = {
        flag: 1,
        audit_remark: ""
    }

    lists = [];

    tabCount = {
        all: 0, //全部
        wait: 0, //待审核
        pass: 0, //审核通过
        refuse: 0, //已拒绝
    };

    pager = new RequestPaging();
    activeName: any = "all";

    confirm(id: number) {
        if(this.examine.flag == 1) {
            apiDistributionApplyPass({
                id,audit_remark: this.examine.audit_remark
            }).then(() => {
                this.getList()
            })
        } else {
            apiDistributionApplyRefuse({
                id,audit_remark: this.examine.audit_remark
            }).then(() => {
                this.getList()
            })
        }
    }

    // 获取申请的列表
    getList(page?: number): void {
        page && (this.pager.page = page)
        let status: any = disType[this.activeName] == "0" ? "" : (disType[this.activeName] as any -1);
        this.pager
            .request({
                callback: apiDistributionApplyLists,
                params: {
                    status: status,
                    ...this.queryObj,
                },
            })
            .then((res) => {
                this.tabCount = res?.extend;
            });
    }

    // 重置
    resetQueryObj() {
        Object.keys(this.queryObj).map((key) => {
            this.$set(this.queryObj, key, "");
        });
        this.getList();
    }

    // 去用户详情
    toUser(id: any) {
        this.$router.push({
            path: "/user/user_details",
            query: { id: id },
        });
    }

    created() {
        this.getList();
    }
}
